    justify-content: space-around;
}
.row {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content:space-around;
}
.column {
    background-color: beige;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 30%;
    height: 200px;
    border: 2px solid green;
    border-radius: 5%;
}
.sub_column {
    display: flex;
    justify-content: center;
    align-items: center;
}
